<template>
  <div>
    <form
      class="reply__form"
      @submit.prevent
    >
      <Avatar
        :address="address"
        class="avatar"
      />
      <div class="input-group">
        <textarea
          ref="input"
          v-model="newComment"
          :placeholder="$t('views.TipCommentsView.AddReply')"
          class="form-control reply__input"
          rows="1"
          @keydown.enter.exact.prevent="sendTipComment"
        />
      </div>
      <div class="send-comment">
        <AeButton
          v-if="newComment.trim().length > 0"
          @click="sendTipComment"
        >
          <img
            src="../assets/backArrow.svg"
            class="arrow"
            :title="$t('views.TipCommentsView.Reply')"
          >
        </AeButton>
      </div>
    </form>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import autosize from 'autosize';
import { EventBus } from '../utils/eventBus';
import { USE_DEEP_LINKS, createDeepLinkUrl } from '../utils/util';
import { wallet } from '../utils/walletSearch';
import AeButton from './AeButton.vue';
import Avatar from './Avatar.vue';
import Backend from '../utils/backend';

export default {
  name: 'SendComment',
  components: {
    AeButton,
    Avatar,
  },
  props: {
    tipId: { type: [Number, String], required: true },
    parentId: { type: [Number, String], default: undefined },
  },
  data() {
    return {
      newComment: '',
      address: null,
      USE_DEEP_LINKS,
    };
  },
  computed: mapGetters(['account', 'isLoggedIn']),
  created() {
    const loadUserAvatar = setInterval(() => {
      if (this.isLoggedIn) {
        this.address = this.account;
        clearInterval(loadUserAvatar);
      }
    }, 500);
  },
  mounted() {
    autosize(this.$refs.input);
  },
  methods: {
    async sendTipComment() {
      if (this.USE_DEEP_LINKS || !this.isLoggedIn) {
        window.location = createDeepLinkUrl(
          {
            type: 'comment', id: this.tipId, text: this.newComment, parentId: this.parentId,
          },
        );
        return;
      }
      await Backend.sendTipComment(
        this.tipId,
        this.newComment,
        wallet.client.rpcClient.getCurrentAccount(),
        (data) => wallet.signMessage(data),
        this.parentId,
      );
      EventBus.$emit('reloadData');
      this.newComment = '';
    },
  },
};
</script>

<style lang="scss" scoped>
  .avatar {
    margin-right: 1rem;
  }

  .reply__form {
    display: flex;
    position: relative;
  }

  .reply__input {
    width: 100%;
    padding-right: 1.3rem;
  }

  .send-comment .ae-button {
    padding: 0;
    height: 2.1rem;
    background-color: transparent;
    z-index: 10;
    min-width: 1rem;
    right: 0.2rem;

    @include vertical-align(absolute);

    img {
      width: 0.8rem;
      color: white;
      transform: rotate(180deg);
      vertical-align: baseline;
    }
  }
</style>
